import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';

export interface ReviewModel {
  rating: number;
  reviewtext: string;
  email: string;
}

@Component({
  selector: 'app-review-form',
  templateUrl: './review-form.component.html',
  styleUrls: ['./review-form.component.css'],
})
export class ReviewFormComponent implements OnInit {
  showEmailPopup = false;
  rating: boolean[] = Array(5).fill(false);
  dataSaved = false;
  reviewText = '';
  emailAddress = '';
  isEmailValid = true;
  addedrating = 0;
  requestBody: ReviewModel;
  constructor(    public dialogRef: MatDialogRef<ReviewFormComponent>) {}

  ngOnInit() {}

  hoverOnStars(index: number) {
    let i = 0;
    while (index >= i) {
      this.rating[i] = true;
      i++;
    }
  }

  chooseRating(star: number) {
    if (this.rating[star]) {
      let i = this.rating.length;
      while (i > star) {
        this.rating[i] = false;
        i--;
      }
    } else {
      this.rating.fill(false);
      let i = 0;
      while (i <= star) {
        this.rating[i] = true;
        i++;
      }
    }
    this.addedrating = star;
    this.dataSaved = true;
  }

  removeHighlight() {
    if (!this.dataSaved) {
      this.rating.fill(false);
    }
  }

  submitRating() {
    this.showEmailPopup = true;

    this.requestBody = {
      rating : this.addedrating,
      reviewtext: this.reviewText,
      email: this.emailAddress
    };
  }

  verifyEmail() {
    const reg = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;

    if (reg.test(this.emailAddress) === false && this.emailAddress) {
      this.isEmailValid = false;
    } else {
      this.isEmailValid = true;
    }
  }

  submit() {
    this.submitRating();
    console.log(this.requestBody);
    this.dialogRef.close();
  }

  closePopup() {
    this.submit();
  }
}
